#overflow-wrapper {
  overflow: scroll;
  overflow-x: hidden;
  height: 400px;
  width: 381px;
  background-color: #dafdfd;
}

#current-bird {
  z-index: 0;
  box-shadow: 5px 5px 0px 10px black inset;
}

#bird-display-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button-wrapper {
  position: relative;
}

.button-container {
  width: 200px;
  height: 200px;
  background-color: rgba(128, 128, 128, 0.75);
  border: 1px black solid;
  border-radius: 30%;
  z-index: 1;
  top: 0px;
  left: 230px;
  position: absolute;
}

@media (max-width: 768px) {
  .button-container {
    top: 230px;
    left: -100px;
    position: absolute;
  }
}

.previous-button,
.next-button {
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0;
  border: 0px;
  background-color: transparent;
  background-image: url("https://static.thenounproject.com/png/2879647-200.png");
}

.previous-button {
  left: 0;
  background-position: 0 0;
  border-radius: 43% 0% 0% 43%;
}

.next-button {
  right: 0;
  background-position: 100% 0;
  border-radius: 0% 43% 43% 0%;
}

.next-button:hover {
  box-shadow: 5px 0px 5px rgb(133, 133, 133);
}

.previous-button:hover {
  box-shadow: -5px 0px 5px rgb(133, 133, 133);
}
